*{
  margin: 0; padding: 0;
  outline: none; border: none;
}

/* ========= Header ========= */
.Navbar {
  width: 100%;
  padding: 1.4rem;
  text-align: end;
  font-size: 1.8rem;
  background: rgb(194, 213, 225);
}

.Navbar a {
  text-decoration: none;
  margin-right: 3rem;
}

.Navbar a:hover {
  text-decoration: underline;
  color: rgb(3, 7, 230);

}

.logo{
  border-radius: 50%;
  border: .1rem solid darkblue;
  width: 11rem;
  height: 5rem;
  line-height: 5rem;
  text-align: center;
  font-size: 2rem; 
  font-weight: bold;
  color: darkblue;
  box-shadow: 0 .3rem .7rem rgb(0, 0, 0, .4);
}

.active{
  color: rgb(87, 141, 177);
}

/* ========= Home ========= */

.Home {
  width: 1200px;
}

.buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 2rem;
}
.btn_search, .btn_sort{
  margin: .3rem;
  padding: .5rem .8rem;
  font-size: 1.1rem;
  color: darkblue;
  border-radius: .5rem;
  border: .1rem solid darkblue;
  cursor: pointer;
}

.search {
  margin-right: 4rem;
}

.btn_sort {
  padding: .2rem .5rem;
  margin-left: 1.2rem;
}

.btn_search:hover, .btn_sort:hover{
  background:  rgb(194, 213, 225);
  box-shadow: 0 .5rem .5rem rgb(84, 102, 113);
}

.Search input, .Search2 input{
  padding: .5rem 1rem;
  border-radius: .5rem;
  border: .1rem solid darkblue;
  font-size: 1rem;
  color: darkblue;
}

.img{ 
  width: 100px;
  height: 100px;
}

.CategoryList {
  max-width: 1200px;
  display: flex;
  justify-content: start;
  flex-wrap: wrap;
}

.CategoryItem, .MealItem {
  padding: 1rem;
  border-radius: .5rem;
  box-shadow: 0 .5rem 1rem rgb(0, 0, 0, .7);
  margin: 1rem;
  font-size: 1.2rem;
  color: darkblue;
}

.CategoryItem .btn {
  text-decoration: none;
  color: darkblue;
  font-weight: 700;
}

.CategoryItem .btn:hover {
  color: rgb(83, 51, 242);
  text-decoration: underline;
}

.CategoryItem:hover {
  box-shadow: 0 .5rem 1rem rgb(0, 0, 0, .4);
}

.btn_item{
  border: none;
  border-bottom: .1rem solid darkblue;
  color: darkblue;
  background: none;
  cursor: pointer;
  font-size: 1rem;
  margin-left: .3rem;
  margin-bottom: 1rem;
}

.btn_item:hover{
  color: rgb(83, 51, 242);
  border-bottom: .1rem solid rgb(83, 51, 242);
}

/* ========= Category ========= */

.Category .btn_search {
  margin-left: 1.2rem;
  margin-top: 1.2rem;
}

.MealList {
  display: flex;
  flex-wrap: wrap-reverse;
}

.MealItem {
  width: 25%;
}

.MealItem:hover {
  box-shadow: 0 .5rem 1rem rgb(0, 0, 0, .4);
}


.MealItem a {
  text-decoration: none;
  color: darkblue;
  margin-left: 3rem;
  font-weight: 600;
}

.MealItem a:hover {
  color: rgb(83, 51, 242);
  text-decoration: underline;
}

.strMealThumb {
  border-radius: .8rem;
}

.idMeal {
  color: darkblue;
  padding: .5rem;
}

/* ========= Recipe ========= */
.Recipe {
  height: 100vh;
  padding: 1.2rem;
  display: flex;
  align-items: flex-start;
  gap: 1rem;
}

.Recipe .strMealThumb{
  width: 400px;
  margin-top: 4rem;
  margin-left: 4rem;
}

.Recipe-text {
  color: darkblue;
  font-size: 1.2rem;
  margin-top: 4rem;
  margin-left: 3rem;
  text-align: center;
}

/* ========= About ========= */

.About {
  height: 100vh;
}

.About p {
  font-size: 3rem;
  text-align: center;
  color: darkblue;
  line-height: 40vh;
}

/* ========= Contact ========= */
.Contact {
  height: 100vh;
}

.Contact p {
  font-size: 1.2rem;
  color: darkblue;
  text-align: end;
  padding: 2rem;
}
 
/* ==========  Footer ========= */
.Footer {
  background: rgb(194, 213, 225);
  text-align: center;
  padding: 1rem;
  color: darkblue;
  font-size: 1.2rem;
}

